import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import "./index.scss";
import "bootstrap/scss/bootstrap.scss";
import "./animate.min.css";
import "./common.sass";
import registerServiceWorker from "./registerServiceWorker";
import App from "./components/pages/App/App";
import Empresa from "./components/pages/Empresa";
import Orcamento from "./components/pages/Orcamento";
import Blog from "./components/pages/Blog";
import BlogInterno from "./components/pages/BlogInterno";
import NotFound from "./components/pages/NotFound";
import EmailMarketing from './components/pages/Inbound/EmailMarketing';
import GoogleAnalytics from "./components/pages/Inbound/GoogleAnalytics";
import GoogleTagManager from './components/pages/Inbound/GoogleTagManager';
import MarketingConteudo from './components/pages/Inbound/MarketingConteudo';
import SEO from './components/pages/Inbound/SEO';
import YoutubeAds from './components/pages/Ads/YoutubeAds';
import Remarketing from './components/pages/Ads/Remarketing';
import LinkedinAds from './components/pages/Ads/LinkedinAds';
import GoogleAdwords from './components/pages/Ads/GoogleAdwords';
import FacebookAds from './components/pages/Ads/FacebookAds';
import BlogCorporativo from './components/pages/Design/BlogCorporativo';
import Branding from './components/pages/Design/Branding';
import UX from './components/pages/Design/UX';
import LojaVirtual from './components/pages/Design/LojaVirtual';
import Site from './components/pages/Design/Site';
import UI from './components/pages/Design/UI';

ReactDOM.render(
  <Router>
    <Switch>
      <Route exact path="/" component={App} />
      <Route exact path="/empresa" component={Empresa} />
      <Route exact path="/orcamento" component={Orcamento} />
      <Route exact path="/blog" component={Blog} />
      <Route exact path="/blog-interno" component={BlogInterno} />
      <Route exact path="/facebook-ads" component={FacebookAds} />
      <Route exact path="/google-adwords" component={GoogleAdwords} />
      <Route exact path="/linkedin-ads" component={LinkedinAds} />
      <Route exact path="/remarketing" component={Remarketing} />
      <Route exact path="/youtube-ads" component={YoutubeAds} />
      <Route exact path="/email-marketing" component={EmailMarketing} />
      <Route exact path="/google-analytics" component={GoogleAnalytics} />
      <Route exact path="/google-tag-manager" component={GoogleTagManager} />
      <Route exact path="/marketing-conteudo" component={MarketingConteudo} />
      <Route exact path="/seo" component={SEO} />
      <Route exact path="/blog-corporativo" component={BlogCorporativo} />
      <Route exact path="/branding" component={Branding} />
      <Route exact path="/ux" component={UX} />
      <Route exact path="/loja-virtual" component={LojaVirtual} />
      <Route exact path="/site" component={Site} />
      <Route exact path="/ui" component={UI} />
      <Route exact path="**" component={NotFound} />
    </Switch>
  </Router>,
  document.getElementById("root")
);
registerServiceWorker();



