import React, { Component, Fragment } from "react";
import "./App.scss";
import NavBar from './../../layouts/common/NavBar/NavBar';
import MapaSite from './../../layouts/common/MapaSite/MapaSite';
import RedesSociais from './../../layouts/common/RedesSociais/RedesSociais';
import Footer from './../../layouts/common/Footer/Footer';
import { Link } from 'react-router-dom';
import BannerTitle from './../../layouts/common/BannerTitle/BannerTitle';
import NoticiasBlog from './../../layouts/App/NoticiasBlog/NoticiasBlog';
import Solucoes from './../../layouts/App/Solucoes/Solucoes';
import VideoBackground from './../../layouts/App/VideoBackground/VideoBackground';
import Apresentacao from './../../layouts/App/Apresentacao/Apresentacao';

const description = `Lorem ipsum dolor sit amet, consectetuer adipiscing
elit, sed diam nonummy nibh euismod tincidunt ut
laoreet dolore magna aliquam erat volutpat.`;

class App extends Component {
  lastNews = {
    titulo: "ltimas Notcias do Blog",
    noticias: [
      { id: 1, title: "Lorem Ipsum", description, imgUrl: "/img/blog1.jpg", link:"/blog-interno", categoryName :"MarketingDigital" },
      { id: 2, title: "Lorem Ipsum", description, imgUrl: "/img/blog2.jpg", link:"/blog-interno", categoryName :"SEO" },
      { id: 3, title: "Lorem Ipsum", description, imgUrl: "/img/blog3.jpg", link:"/blog-interno", categoryName :"GoogleAdwords" }
    ]
  };

  topNews = {
    titulo: "Em Alta no Blog",
    noticias: [
      { id: 4, title: "Lorem Ipsum", description, imgUrl: "/img/blog1.jpg", link:"/blog-interno", categoryName :"SEO" },
      { id: 5, title: "Lorem Ipsum", description, imgUrl: "/img/blog2.jpg", link:"/blog-interno", categoryName :"Design" },
      { id: 6, title: "Lorem Ipsum", description, imgUrl: "/img/blog3.jpg", link:"/blog-interno", categoryName :"GoogleAdwords" }
    ]
  };

  render() {
    return (
      <Fragment>
        {/* Configuro landscape */}
        <div id="lying" />

        {/* Configuro natural */}
        <div id="standing">
          <div id="wrapper-main" className="opening">
            <header>
              <NavBar />
            </header>

            {/* O dgito que faltava na sua estratgia de Marketing Digital */}
            <main>
              <section id="wrapper-banner">
                <VideoBackground />
                <BannerTitle>
                  <h1>
                    O <strong>dgito</strong> que faltava na sua{" "}
                    <strong>estratgia</strong> de{" "}
                    <strong>Marketing Digital</strong>
                  </h1>
                </BannerTitle>
              </section>

              {/* Venha para a dgitozero */}
              <section id="wrapper-master">
                <div className="master">
                  <div className="container">
                    <div className="row">
                      <Apresentacao />
                    </div>
                  </div>
                </div>

                {/* Solues: Design, Inbound, Ads */}
                <div className="featured">
                  <div className="container">
                    <div className="row">
                      <Solucoes />
                    </div>
                  </div>
                </div>

                <div className="section app-fenda-top">
                  <div className="container">
                    <div className="row">
                      <NoticiasBlog {...this.lastNews} />
                      <NoticiasBlog {...this.topNews} />

                      <div className="col-12">
                        <Link to="/blog" className="button area">
                          Confira nosso Blog
                        </Link>
                      </div>
                    </div>
                  </div>
                </div>
              </section>

              <section id="wrapper-map" className="featured app-fenda-top">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default App;



