import React, { Component } from "react";
import { Link } from 'react-router-dom';

class NavSubItem extends Component {
  render() {
    return (
      <li>
        <Link to={this.props.link}>{this.props.title}</Link>
      </li>
    );
  }
}

export default NavSubItem;



