import React, { Component } from 'react'

export default class FormGoogleTagManager extends Component {
  render() {
    return (
        <form className="form-default">
        <div className="container">
          <div className="row">
            <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
              <div className="row">
                <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label for="txt-nome" className="lbl-title">
                      Seu nome?*
                    </label>
                    <input
                      type="text"
                      className="form-control border"
                      id="txt-nome"
                      required=""
                    />
                  </div>
                </div>
                <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label
                      for="txt-email"
                      className="lbl-title"
                    >
                      Seu e-mail?*
                    </label>
                    <input
                      type="email"
                      className="form-control border"
                      id="txt-email"
                      required=""
                    />
                  </div>
                </div>
                <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label
                      for="txt-empresa"
                      className="lbl-title"
                    >
                      Nome da empresa?*
                    </label>
                    <input
                      type="text"
                      className="form-control border"
                      id="txt-empresa"
                      required=""
                    />
                  </div>
                </div>
                <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label for="txt-site" className="lbl-title">
                      Site da empresa?
                    </label>
                    <input
                      type="site"
                      className="form-control border"
                      id="txt-site"
                      required=""
                    />
                  </div>
                </div>

                <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content">
                    <label
                      for="txt-necessidade"
                      className="lbl-title"
                    >
                      Nos diga sua necessidade
                    </label>
                    <textarea
                      id="txt-necessidade"
                      className="form-control"
                    />
                  </div>
                </div>
                <div className="wrapper-content">
                  <button className="button btn-send">
                    {" "}
                    Enviar
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </form>
    )
  }
}



