import React, { Component } from "react";
import NavItem from "./NavItem/NavItem";
import NavMainSubItem from "./NavMainSubItem/NavMainSubItem";
import NavSubItem from './NavSubItem/NavSubItem';
import "./NavBar.sass";
import { Link } from 'react-router-dom';
import { DigitoZeroSVG } from './../../../SVG/DigitoZero';

export default class NavBar extends Component {

  constructor(props){
    super(props)
    this.state = {
      pageIsScrolled : window.pageYOffset > 100 ? true : false,
      listHovered : false,
    }
  }

  handleScroll = (event) => {
    var scrollTop = window.pageYOffset | event.srcElement.body.scrollTop
    const pageScrolledByUser = scrollTop > 100
    const needChangeState = this.state.pageIsScrolled !== pageScrolledByUser
    if ( needChangeState ){
      this.setState({pageIsScrolled : pageScrolledByUser})
    }
  }

  componentDidMount = () => {
    window.addEventListener('scroll', this.handleScroll);
  }

  componentWillUnmount = () => {
      window.removeEventListener('scroll', this.handleScroll);
  }

  handleHover = (event) => {
    if( event.type === "mouseenter") this.setState({listHovered:true})
    else if(event.type === "mouseleave") this.setState({listHovered:false})
  }

  addClasses = () => {
    let classes = "";
    classes += this.state.pageIsScrolled ? "scroll-on-page" : ""
    classes += " "
    classes += this.state.listHovered ? "active" : ""
    return classes;
  }

  render() {
    return (
      <nav className={this.addClasses()} id="wrapper-menu">
        <div className="container">
          <div className="row">
            <div className="col-xl-2 col-lg-2 col-md-12 col-sm-12 col-12">
              <DigitoZeroSVG />
            </div>
            <div className="col-xl-10 col-lg-10 col-md-12 col-sm-12 col-12">
              <ul>
                <NavItem title="Empresa" link="/empresa" />

                <span onMouseEnter={this.handleHover} onMouseLeave={this.handleHover}>
                  <NavItem title="Servios" link="/servicos">
                    <NavMainSubItem link="/design" title="Design">
                      <NavSubItem link="/branding" title="Branding" />
                      <NavSubItem link="/ux" title="UX" />
                      <NavSubItem link="/ui" title="UI" />
                    </NavMainSubItem>

                    <NavMainSubItem link="/desenvolvimento" title="Desenvolvimento">
                      <NavSubItem link="/Site" title="Site" />
                      <NavSubItem link="/loja-virtual" title="Loja Virtual" />
                      <NavSubItem link="/App" title="App" />
                    </NavMainSubItem>

                    <NavMainSubItem link="/inbound" title="Inbound Markting">
                      <NavSubItem link="/SEO" title="SEO" />
                      <NavSubItem link="/marketing-conteudo" title="Marketing de Contedo" />
                      <NavSubItem link="/blog-corporativo" title="Blog Corporativo" />
                      <NavSubItem link="/email-marketing" title="Email Marketing" />
                      <NavSubItem link="/google-analytics" title="Google Analytics" />
                      <NavSubItem link="/google-tag-manager" title="Google Tag Manager" />
                    </NavMainSubItem>

                    <NavMainSubItem link="/ads" title="Publicidade Online">
                      <NavSubItem link="/patrocinados" title="Links Patrocinados" />
                      <NavSubItem link="/facebook-ads" title="Facebook Ads" />
                      <NavSubItem link="/remarketing" title="Remarketing" />
                      <NavSubItem link="/linkedin-ads" title="Linkedin Ads" />
                    </NavMainSubItem>
                  </NavItem>
                </span>

                <NavItem title="Blog" link="/blog" />
                <NavItem title="Contato" link="/contato" />

                <li className="featured">
                  <Link to="/orcamento">Quero um oramento</Link>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </nav>
    );
  }
}



