import React, { Component } from "react";

export default class DesignSVG extends Component {
  render() {
    return (
      <svg viewBox="0 0 128 128">
        <polygon
          className="stroke-dark-blue stroke"
          points="26,42.2 64,64.1 102,42.2 64,20.2 "
        />
        <polygon
          className="stroke-magenta-violet stroke"
          points="26,42.2 26,86.1 64,108 64,64.1 "
        />
        <polygon
          className="stroke-blue-cloud stroke"
          points="64,108 64,64.1 102,42.2 102,86.1 "
        />
      </svg>
    );
  }
}



